@import url('https://fonts.googleapis.com/css2?family=Quando&display=swap');
.font {
  font-family: 'Quando', -apple-system, BlinkMacSystemFont, 'Helvetica Neue',
    sans-serif, 'sans-serif';
}

.scrollBar {
  scrollbar-width: none;
  scroll-behavior: smooth;
}

.scrollBar::-webkit-scrollbar {
  display: none;
}

.signupScrollBar {
  scrollbar-width: thin;
  scroll-behavior: smooth;
}

.signupScrollBar::-webkit-scrollbar {
  width: 12px;
}

.signupScrollBar::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}

.signupScrollBar::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.418);
}

@media all and (max-width: 500px) {
  .signupScrollBar {
    scrollbar-width: none;
    scroll-behavior: smooth;
  }

  .signupScrollBar::-webkit-scrollbar {
    width: 0px;
  }
}
